.header {
  @include transition(all);

  & .container {
    padding-top: 40px;
    padding-bottom: 44px;
    padding-left: 20px;
    padding-right: 20px;

    @media screen and (min-width: 768px) {
      padding-top: 47px;
      padding-bottom: 44px;
      padding-left: 32px;
      padding-right: 32px;
    }
    @media screen and (min-width: 1024px) {
      padding-top: 45px;
      max-width: 1600px;
    }
  }
}

.header-home {
  height: 230px;
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url(/images/header-bg/header-home-bg-mob-1x.jpg);

  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url(/images/header-bg/header-home-bg-mob-2x.jpg);
  }

  @media screen and (min-width: 768px) {
    height: 219px;
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url(/images/header-bg/header-home-bg-tab-1x.jpg);

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: linear-gradient(to top, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
        url(/images/header-bg/header-home-bg-tab-2x.jpg);
    }
  }

  @media screen and (min-width: 1024px) {
    // max-width: 1600px;
    // margin: 0 auto;
    height: 216px;
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url(/images/header-bg/header-home-bg-desktop-1x.jpg);

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: linear-gradient(to top, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
        url(/images/header-bg/header-home-bg-desktop-2x.jpg);
    }
  }
}

