.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 24px;
  margin-bottom: 50px;

  @media screen and (min-width: 768px) {
    margin-bottom: 36px;
  }

  @media screen and (min-width: 1024px) {
    margin-bottom: 37px;
  }
}

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
  cursor: pointer;
  @include transition(all);
}

.site-title {
  color: $whiteColor;
  font-family: $mainFont;
  font-style: normal;
  font-weight: 500;
  font-size: 30px;
  line-height: 1.16;
  visibility: hidden;
  width: 0;

  @media screen and (min-width: 768px) {
    visibility: visible;
    margin-left: 10px;
    width: auto;
  }

  @media screen and (min-width: 1024px) {
    visibility: visible;
    margin-left: 12px;
  }
  &:hover {
    color: $orangeColor;
    @include transition(all);
  }
}

.nav__buttons {
  display: flex;
}

.nav__btn {
  position: relative;
  color: $whiteColor;
  font-family: $mainFont;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.16;
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  border: none;
  background: transparent;
  padding: 0;
  cursor: pointer;
  @include transition(all);

  &:not(:last-child) {
    margin-right: 40px;
  }
}

.current::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 3px;
  //border-radius: 2px;
  background-color: $orangeColor;
  display: block;
  bottom: -3px;
  @include transition(all);
}
